import React, { Component } from "react";

class Input extends Component {
    constructor() {
        super();
    }
    changeName = (event)=>{
        this.props.onChange(event.target.value);
    }
    render() {
        console.log(this.props.onChange);
        return (
            <div>
                <input placeholder="name" onChange={this.changeName}/>
            </div>
        )
    }
}

export default Input;