import React, { Component } from "react";

class Header extends Component {
    constructor() {
        super();
        this.state={name :"Accolite Digital"};
    }
    changeName = (name) =>{
        this.setState({name});

    }
    render() {
        return (
            <div>
                <h2>Welcome Mr. {this.state.name}</h2>
            </div>
        )
    }
}

export default Header;